const fakeFetch = (id) => {
  return new Promise(
    (resolve, reject) => { setTimeout(() => resolve(_products[id]), 100) }
  )
}

const fakeFetchProducts = () => {
  return new Promise(
    (resolve, reject) => { setTimeout(() => resolve(_products, 100) )}
  )
}

export async function retrieveProducts () {
  const data = await fakeFetchProducts ()
  return data
}

export async function retrieveProduct (product_id) {
  const data = await fakeFetch (product_id)
  return data
}

