<template>
  <div>
    <form @submit.prevent="createContact(contact)">
      <input id="subject" :value="contact.subject" @input="updateLocaleContact($event);">
      <input id="body" :value="contact_form.body" @input="updateLocaleContact($event);">
      <input type="submit" value="Submit"/>
    </form>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  data () {
    return {
      contact: {subject: '', body: ''}
    }
  },
  methods: {
    createContact (Contact) {
      this.$store.dispatch('createContact', contact)
      this.$router.push("/")
    },
    updateLocaleContact(e) {
      this.$set(this.contact, e.target.id, e.target.value)
    }
    //...mapGetters(["activeProduct"])
  }
}
</script>
